import { Injectable } from '@angular/core';
import * as XLSX from 'xlsx';
import * as ExcelJS from 'exceljs';
import { ExportToExcelOptions, Section } from '../shared/Interfaces/IExcel';
@Injectable({
  providedIn: 'root'
})
export class ExcelService {
  constructor() { }
  exportToExcel(data, headings, exportedFileName = new Date().getMilliseconds().toString()) {
    //Had to create a new workbook and then add the header
    const wb = XLSX.utils.book_new();
    const ws: XLSX.WorkSheet = XLSX.utils.json_to_sheet([]);
    XLSX.utils.sheet_add_aoa(ws, headings);

    //Starting in the second row to avoid overriding and skipping headers
    XLSX.utils.sheet_add_json(ws, data, { origin: 'A2', skipHeader: true });

    XLSX.utils.book_append_sheet(wb, ws, 'Sheet1');

    XLSX.writeFile(wb, `${exportedFileName}.xlsx`);
  }
  exportToExcelJs(exportToExcelOptions: ExportToExcelOptions) {
    const workbook = new ExcelJS.Workbook();
    const worksheet = workbook.addWorksheet('Sheet 1');
    if (exportToExcelOptions.mainHeading) {
      // Add main heading
      const mainHeadingRow = worksheet.addRow([exportToExcelOptions?.mainHeading]);
      mainHeadingRow.font = { bold: true, size: 18, color: { argb: 'ff000000' } };
      worksheet.addRow([]); // Add an empty row for spacing after the main heading
    }
    // Function to add a section with data and a gap after each section
    const addSection = (section: Section) => {
      if (section.heading.length) {
        const heading = worksheet.addRow(section.heading);
        heading.font = { bold: true, size: 14, color: { argb: 'ff000000' } };
      }

      if (section.showAsKeyValue) {
        // Display as key-value pairs (one row per header/data)
        section.headers.forEach((header, index) => {
          const row = worksheet.addRow([header, section.data[index]]); // Add key (header) in one cell and value (data) in the next
          row.getCell(1).font = { bold: true, }; // Make the key (header) bold
        });
      } else {
        // Display data in table format (headers as the first row, followed by rows of data)
        const headerRow = worksheet.addRow(section.headers); // Add section header
        headerRow.font = { bold: true, size: 12, color: { argb: 'ff000000' } };
        section.data.forEach(row => {
          worksheet.addRow(row); // Add data rows
        });
      }

      worksheet.addRow([]); // Add gap after each section
    };

    // Add sections with data
    exportToExcelOptions.sections.forEach(section => {
      addSection(section);
    });

    // Generate Excel file
    workbook.xlsx.writeBuffer().then((buffer: ArrayBuffer) => {
      const blob = new Blob([buffer], { type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' });
      const url = window.URL.createObjectURL(blob);
      const a = document.createElement('a');
      a.href = url;
      a.download = exportToExcelOptions.fileName + '.xlsx';
      a.click();
      window.URL.revokeObjectURL(url);
    });
  }
}
